.loader {
  flex-direction: column;
  height: 100px;
  margin: 15px;
}

.container {
  width: 30%;
  height: 60px;
  text-align: center;
}

.speedingWheel {
  width: 60px;
  height: 60px;
  margin: 0 auto;
  border: 4px solid rgb(7, 1, 1);
  border-radius: 50%;
  border-left-color: transparent;
  border-right-color: transparent;
  animation: my_spinner 800ms infinite linear;
  -webkit-animation: my_spinner 800ms infinite linear;
  -moz-animation: my_spinner 800ms infinite linear;
}


@keyframes my_spinner {
  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes my_spinner {
  100% {
    transform: rotate(360deg);
  }
}

@-moz-keyframes my_spinner {
  100% {
    transform: rotate(360deg);
  }
}